textarea {
  border-radius: 8px;
  border: 1px solid var(--gray-300, #d0d5dd);
  transition: all 0.3s ease-in-out;
  border: 0;
  border-radius: var(--radius, 8px);
  padding: 10px 14px;
  margin: 0;
  width: 100%;
  font-size: 16px;
}
textarea:focus {
  border-color: var(--brand-300, #84caff);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 4px var(--brand-100, #d1e9ff);
  outline: none;
}
.error {
  border-color: var(--error-300, #fda29b);
}
textarea:disabled {
  background: var(--gray-50, #f9fafb);
  color: var(--gray-300, #d0d5dd);
  pointer-events: none;
}
